'use strict';

/**
 * Sushi mission
 */

angular
    .module('sushi.org.missions', [
      'ui.router',
      'ui.bootstrap',
      'sushi.common'
    ])
    .config(function ($stateProvider, $urlRouterProvider) {
      $stateProvider
          .state('sushi.viewer.Mission', {
            url: '/mission',
            views: {
              'summary':{
                templateUrl: 'sushiPacks/sushi-org-missions/view.html',
                controller: 'MissionViewerCtrl'
              }
            },
            breadcrumb: {
              class: 'highlight',
              text: 'Sushi Mission',
              stateName: 'sushi.viewer.Mission'
            }
          })
          .state('sushi.editor.Mission', {
            url: '/mission',
            views: {
              'summary':{
                controller: 'MissionViewerCtrl',
                templateUrl: 'sushiPacks/sushi-org-missions/view.html',
              }
            },
            breadcrumb: {
              class: 'highlight',
              text: 'Sushi Mission Editor',
              stateName: 'sushi.editor.Mission'
            }
          });
    });



