'use strict';

/**
 * Sushi result
 */

angular
    .module('sushi.org.results', [
      'ui.router',
      'ui.bootstrap',
      'sushi.common'
    ])
    .config(function ($stateProvider, $urlRouterProvider) {
      $stateProvider
          .state('sushi.viewer.Result', {
            url: '/result',
            views: {
              'summary':{
                templateUrl: 'sushiPacks/sushi-org-results/view.html',
                controller: 'ResultViewerCtrl'
              }
            },
            breadcrumb: {
              class: 'highlight',
              text: 'Sushi Result',
              stateName: 'sushi.viewer.Result'
            }
          })
          .state('sushi.editor.Result', {
            url: '/result',
            views: {
              'summary':{
                controller: 'ResultViewerCtrl',
                templateUrl: 'sushiPacks/sushi-org-results/view.html',
              }
            },
            breadcrumb: {
              class: 'highlight',
              text: 'Sushi Result Editor',
              stateName: 'sushi.editor.Result'
            }
          });
    });



